import React from 'react';

const Tips = () => (
  <div style={{textAlign: 'left', margin: '30px', width: '900px'}}>
    <h2>Fr du bruker applikasjonen</h2>
    <br/>
    <h4>Cross-Origin Resource Sharing</h4>
    <p>JIRA pner ikke for CORS p REST-kall ifra ukjente domener, som gjr at du m deaktivere sikkerhetsmekanismer i nettleseren din for  bruke applikasjonen <i>(inntil videre)</i>.</p>
    <p>&nbsp;&nbsp; Srg for at du ikke har noen instanser av Chrome som kjrer p maskinen.</p>
    <p>&nbsp;&nbsp;Linux: <code>google-chrome --disable-web-security --user-data-dir</code></p>
    <p>&nbsp;&nbsp;Windows: <code>"C:\Program Files (x86)\Google\Chrome\Application\chrome.exe" --disable-web-security --user-data-dir</code></p>
    <p>Jeg ville forsvidt ikke anbefale  surfe for mye p internett i denne tilstanden.</p>
    <br/>
    <h4>Login</h4>
    <p>Login krever samme passord of brukernavn som JIRA brukeren din. Om du mter problemer med  logge inn kan det hende at CAPTCHA-systemet til JIRA har blitt aktivert, i hvilket tilfelle er du ndt til  beske JIRA og <i>(logge ut og)</i> logge inn.</p>
    <br/>
    <h4>Feedback</h4>
    <p>Om du har noe p hjertet kan du kontakte meg via <i>Martin.Jonassen@skatteetaten.no</i></p>
  </div>
);

export default Tips;


