import { MOTTA_UTGIVELSER } from '../handlinger/handlingsTyper';

const utgivelser = (state = [], action) => {
  switch (action.type) {
    case MOTTA_UTGIVELSER:
      return action.utgivelser;
    default:
      return state;
  }
}

export default utgivelser;


