import MAP, { PROSJEKT } from '../segmentKart';
import * as typer from './handlingsTyper';

export const URL = 'https://aurora/jira';

export const mottaOppgaver = ({faneId, segmentId}, oppgaver) => ({
  type: typer.MOTTA_OPPGAVER,
  faneId,
  segmentId,
  oppgaver: oppgaver.map((oppgave) => ({...oppgave, erMarkert: false}))
});

export const mottaUtgivelser = utgivelser => ({
  type: typer.MOTTA_UTGIVELSER,
  utgivelser
});

export const markerSegmentVenter = (faneId, segmentId) => ({
  type: typer.MARKER_SEGMENT_VENTER,
  faneId,
  segmentId
});

export const login = legitimasjon => ({
  type: typer.LOGIN,
  legitimasjon
});

const error = error => ({
  type: typer.ERROR,
  error: error.toString()
});

export const nettverksFeil = error => ({
  type: typer.FEILMELDING_NETTVERK,
  error: parseNettverksFeil(error)
});

export const parseNettverksFeil = respons => {
  switch(respons.status) {
    case 400:
      return '400 Bad Request: Sket ditt har feil format';
    case 401:
      return '401 Unauthorized: Brukernavnet eller passordet ble ikke godkjent';
    case 403:
      return '403 Forbidden: Du har aktivert JIRAs CAPTCHA system';
    default:
      return `${respons.status} ${respons.statusText}`;
  }
};

export const lagUrl = ({faneId, segmentId}, state) => {
  let url = `${URL}/rest/api/2/search?jql=`;
  url += (MAP[faneId][segmentId].start)
    ? MAP[faneId][segmentId].formaterUrl()
    : MAP[faneId][segmentId].formaterUrl(
        state.faner[faneId][segmentId - 1].oppgaver
          .filter(oppgave => oppgave.erMarkert)
          .map(oppgave => oppgave.key)
      )
  if (state.faner[faneId][segmentId].soekefelt) url += ` AND ${state.faner[faneId][segmentId].soekefelt}`;
  if (MAP[faneId][segmentId].trengerUtgivelse) url += ` AND fixVersion = "${state.faner[faneId].utgivelse}"`;

  return url;
}

export const fetchOppgaver = (url, legitimasjon) => dispatch => {
  return fetch(url, {
    method: 'GET',
    headers: new Headers({
      'Content-Type': 'application/json',
      'Authorization': 'Basic ' + legitimasjon
    })
  }).catch(dispatch(nettverksFeil));
};

export const hentVersjoner = legitimasjon => dispatch => {
  return fetch(`${URL}/rest/api/2/project/${PROSJEKT}/version?orderBy=startDate`, {
    method: 'GET',
    headers: new Headers({
      'Content-Type': 'application/json',
      'Authorization': 'Basic ' + legitimasjon
    })
  }).catch(dispatch(nettverksFeil));
}

export const hentOppgaver = kontekst => (dispatch, getState) => {
  const {faneId, segmentId} = kontekst;
  dispatch(markerSegmentVenter(faneId, segmentId));
  const url = lagUrl(kontekst, getState());
  dispatch(fetchOppgaver(url, getState().autentisering.legitimasjon)).then(
    respons => {
      if (!respons.ok) {
        dispatch(mottaOppgaver(kontekst, []));
        dispatch(nettverksFeil(respons));
      } else {
        respons.json().then(
          body => dispatch(mottaOppgaver(kontekst, body.issues))
        );
      };
    }
  );
  return Promise.resolve();
}

export const soekOgHentOppgaver = 
  (kontekst, soekeVerdi) => 
    (dispatch, getState) => {
  const { faneId, segmentId } = kontekst;
  dispatch({
    type: typer.FILTRER_SOEK,
    faneId,
    segmentId,
    soekeVerdi
  });
  dispatch(hentOppgaver(kontekst));
}

export const markerOgHentOppgaver =
  (kontekst, oppgaveId) => 
    (dispatch, getState) => {
  const { faneId, segmentId } = kontekst;
  dispatch({
    type: typer.MARKER_OPPGAVE,
    faneId,
    segmentId,
    oppgaveId
  });
  if (MAP[faneId][segmentId].slutt) return;
  dispatch(hentOppgaver({faneId, segmentId: segmentId + 1}));
}

export const markerUtgivelseOgHentOppgaver = (kontekst, utgivelse) => (dispatch, getState) => {
  const { faneId } = kontekst;
  dispatch({
    type: typer.FILTRER_UTGIVELSE,
    faneId,
    utgivelse
  });

  MAP[faneId].forEach((segment, id) => {
    if (!segment.start) return;
    dispatch(hentOppgaver({faneId, segmentId: id}));
  })
  return Promise.resolve();
}

export const autentiser = legitimasjon => (dispatch, getState) => {
  dispatch({ type: typer.LOGIN_VENT })
  dispatch(hentVersjoner(legitimasjon))
    .then(respons => {
      if (!respons.ok) {
        dispatch(nettverksFeil(respons));
      } else {
        respons.json().then(
          body => {
            dispatch(mottaUtgivelser(body.values.filter(({ archived }) => !archived)));
            dispatch(login(legitimasjon));
          }
        );
      }
    });
  return Promise.resolve();
};


