import { combineReducers } from 'redux';
import faner from './faner';
import autentisering from './autentisering';
import utgivelser from './utgivelser';
import error from './error';

const jiraFremviser = combineReducers({
  error,
  autentisering,
  utgivelser,
  faner
});

export default jiraFremviser;


