import {
  LOGIN,
  LOGIN_VENT,
  FEILMELDING_NETTVERK,
  ERROR,
  MOTTA_OPPGAVER,
  MOTTA_UTGIVELSER
} from '../handlinger/handlingsTyper';

const autentisering = (state = {
  legitimasjon: "",
  venter: false
}, action) => {
  switch(action.type) {
    case LOGIN:
      return { legitimasjon: action.legitimasjon, venter: false };
    case LOGIN_VENT:
      return { ...state, venter: true }
    case FEILMELDING_NETTVERK:
    case ERROR:
    case MOTTA_OPPGAVER:
    case MOTTA_UTGIVELSER:
      return { ...state, venter: false }
    default:
      return state;
  }
};

export default autentisering;

