import { FEILMELDING_NETTVERK, ERROR } from '../handlinger/handlingsTyper';

const error = (state = "", action) => {
  switch (action.type) {
    case FEILMELDING_NETTVERK:
    case ERROR:
      return action.error;
    default:
      return "";
  }
}

export default error;


