import React from 'react';
import { Route } from 'react-router-dom';
import PropTypes from 'prop-types';

import Login from '../containere/Login';
import VenteIkon from './VenteIkon';

const LoginCheck = ({ erAutentisert, venter, component: Component, ...rest }) => (
  <Route {...rest} render={props => 
    erAutentisert
    ? <Component {...rest} />
    : venter
      ? <VenteIkon/>
      : <Login/>
  }/>
);

LoginCheck.propTypes = {
  erAutentisert: PropTypes.bool.isRequired,
  venter: PropTypes.bool.isRequired,
  component: PropTypes.func.isRequired
}

export default LoginCheck;


