import React from 'react';
import PropTypes from 'prop-types';

import OppgaveListe from "./OppgaveListe";
import OppgaveJQLFilter from "../containere/OppgaveJQLFilter";
import VenteIkon from './VenteIkon';

const Segment = ({kontekst, segment}) => (
  <div className="ske-segment">
    <OppgaveJQLFilter kontekst={kontekst} />
    {(segment.venter)
    ? <VenteIkon/>
    : (segment.oppgaver && segment.oppgaver.length)
      ? <OppgaveListe oppgaver={segment.oppgaver} kontekst={kontekst} />
      : <div/>
    }
  </div>
);

Segment.propTypes = {
  kontekst: PropTypes.shape({
    faneId: PropTypes.number,
    segmentId: PropTypes.number
  }).isRequired,
  segment: PropTypes.object.isRequired
};

export default Segment;


