import { connect } from 'react-redux';

import LoginCheck from '../komponenter/LoginCheck';

// edit reduserere/legitimasjon.js til reduserere/autentisering.js, state.autentisering = {legitimasjon: "string", venter: bool}
// fjern fra App.js, rediger i handlinger, endre i redusere
const mapStateToProps = state => ({
  erAutentisert: state.autentisering.legitimasjon.length > 0,
  venter: state.autentisering.venter
});

export default connect(mapStateToProps)(LoginCheck);


